body {
  cursor: url("cursor.6c136806.png"), url("cursor2.dfb675bf.cur"), auto;
  background-color: #ffd90067;
}

.header {
  text-align: center;
}

.logo {
  width: 600px;
  height: 250px;
}

.button {
  cursor: pointer;
  background-color: gold;
  border-radius: 5px;
  height: 30px;
}

.button:hover {
  animation-name: aumentarColores;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

@keyframes aumentarColores {
  0% {
    background-color: gold;
    transform: scale(1);
  }

  100% {
    background-color: orange;
    transform: scale(1.5);
  }
}

.info {
  text-align: center;
}

.infoP {
  background-color: #fff;
  border: 3px solid;
  border-radius: 5px;
  width: 350px;
  height: 16px;
  margin: auto auto 20px;
  font-family: DejaVu Sans Mono;
  font-size: 14px;
}

.champSelect {
  background-color: #000 !important;
}

.champSelect:hover {
  animation-name: aumentar;
  animation-duration: .5s;
  animation-fill-mode: forwards;
}

@keyframes aumentar {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.05);
  }
}

.body {
  justify-content: center;
  align-items: center;
  display: flex;
}

#wiki {
  grid-template-columns: repeat(5, 1fr);
  gap: 12px;
  width: 100%;
  display: grid;
}

#mensaje {
  color: #fff;
  visibility: hidden;
}

.description p {
  text-align: justify;
}

.champSelect {
  text-align: center;
  border: 2px solid #4e4e4e;
  border-radius: 5px;
