}

.description:active .imgChamp {
  filter: blur(8px);
  cursor: pointer;
}

.description:active p {
  display: inline;
  top: 0;
}

.nombre {
  font: bold;
  background: linear-gradient(#f8f7a9, #ffae00);
  border: 2px solid;
  margin: 0;
}

.resource {
  background: linear-gradient(#0ff, #008cff);
  border: 2px solid;
}
